
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#delivery {
  margin-top: 20px;
}
#delivery .list {
  background: #fff;
}
#delivery .list > div {
  padding: 10px 20px;
}
#delivery .list .addr {
  border: 1px solid #ddd;
}
#delivery .list .addr .box .addr-col {
  margin-left: 5px;
  background: #0f0;
  color: #fff;
  border-radius: 5px;
  padding: 3px 5px;
}
#delivery .list .addr .col {
  color: #999;
  margin-top: 5px;
}
#delivery .list .menu {
  overflow: hidden;
}
#delivery .list .menu .fl {
  margin-top: 0;
}
#delivery .list .menu .fl .weui-cell {
  padding: 0;
}
#delivery .list .menu .weui-cells:before {
  border-top: none;
}
#delivery .list .menu .weui-cells:after {
  border-bottom: none;
}
#delivery .list .menu .fr span {
  margin-left: 10px;
}
#delivery .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #04b101;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 10px 0;
}



